<template>
  <div>
    <div class="titlec">
      <div class="bigtitlec">
        <div>Flower✦雨之岩</div>
        <div>The Lost Age</div>
      </div>
    </div>

    <div style="width:1000px;margin:20px auto;">
      <div style="width: 700px;float: left;">
        <div class="content" v-for="(item,i) in newsinfo" :key="i">
          <div class="left">
            <img :src="item.avatar_url" alt="" :title="item.html_url" @click="gotogithub(item.html_url)">
          </div>
          <div class="right">
            <div class="title" @click="gotoNewsInfo(item.nid)">{{item.title?item.title:'未命名标题'}}</div>
            <div class="infoma">{{item.updatetime}} ✦ {{item.name}} ✦ {{item.email}}</div>
            <!-- <div class="con" v-html="item.content"></div> -->
            <!-- <div class="allbtn" @click="_setNewsItem(item)">ALL>></div> -->
          </div>
        </div>
      </div>
      <div class="bodyright">
        <div class="personinfo">
          <div v-if="userinfo">
            <div class="plist1">
              <img :src="userinfo.avatar_url" @click="gotogithub(userinfo.html_url)" :title="userinfo.html_url">
              <div style="padding-top: 10px;">{{userinfo.name}}</div>
              <div style="font-size:13px;">{{userinfo.email}}</div>
              <div class="bio">{{userinfo.bio}}</div>
            </div>
            <!-- <div class="plist2" @click="editflg=!editflg">
              <i class="iconfont icon-edit"></i>
              <div>Add New</div>
            </div> -->
            <!-- <div class="plist2">
              <i class="iconfont icon-comments"></i>
              <div>My Msg</div>
            </div> -->
            <div class="plist2" @click="exit">
              <i class="iconfont icon-exit"></i>
              <div>Exit</div>
            </div>
          </div>
          <div v-else>
            <div class="plist2" @click="_login()">
              <i class="iconfont icon-github" style="font-size: 20px;"></i>
              <div>使用github登录</div>
            </div>
          </div>
        </div>
        <!-- <div class="tags">
          <div class="tag" v-for="(item,i) in classify" :key="i">
            <i class="iconfont icon-attachment"></i> {{item.name}}
            <div class="num">{{item.num}}</div>
          </div>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations
  } from 'vuex';

  export default {
    name: "name",
    computed: {
      ...mapState('main', {
        newsinfo: state => state.newsinfo
      })
    },
    created() {
      this.getnews();
    },
    data() {
      return {
        p: undefined,
        userinfo: undefined
      };
    },
    methods: {
      ...mapMutations({
        getnews: 'main/getnews',
        setNewsItem: 'main/setNewsItem'
      }),
      _setNewsItem(item) {
        this.setNewsItem(item)
        this.$router.push('/Article')
      },
      gotogithub(url) {
        window.open(url);
      },
      _login() {
        window.location.href =
          'https://github.com/login/oauth/authorize?client_id=5264e5de73487b1b7a35&redirect_uri=http://101.132.128.112:4000/github';
      },
      gotoNewsInfo(id) {
        localStorage.setItem('nid',id)
        this.$router.push('/Article')
      }
    }
  };

</script>

<style scoped>
  .bio {
    font-size: 13px !important;
    width: 100%;
    padding: 0px 10px;
    line-height: 20px !important;
    margin-bottom: 10px;
  }

  .plist2 div {
    float: left;
    text-indent: 20px;
    font-size: 15px;
  }

  .plist2 i {
    float: left;
  }

  .plist2:hover .icon-github {
    transform: rotate(360deg) scale(1.5);
  }

  .icon-github {
    transition: 0.3s;
  }

  .plist2 {
    box-shadow: 0px 1px 10px #ccc;
    padding: 10px 20px;
    overflow: hidden;
    cursor: pointer;
  }

  .plist1 span {
    font-size: 13px;
  }

  .plist1 div {
    line-height: 23px;
    float: left;
    text-indent: 20px;
    font-size: 20px;
  }

  .plist1 img:hover {
    border-radius: 10px;
  }

  .plist1 img {
    width: 50px;
    height: 50px;
    float: left;
    border-radius: 50px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    margin-bottom: 10px;
    transition: 0.2s;
  }

  .plist1 {
    overflow: hidden;
  }

  .personinfo {
    width: 100%;
    background: #fff;
  }

  .bodyright {
    float: right;
    width: 270px;
  }

  .bigtitlec div:nth-child(1) {
    font-size: 40px;
    font-weight: 300;
  }

  .bigtitlec {
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .titlec {
    height: 300px;
    background: url('../assets/rain.jpg') no-repeat;
    background-size: cover;
    text-align: center;
    position: relative;
  }

  .allbtn:hover {
    color: #fff;
    border-radius: 20px;
  }

  .allbtn {
    background: #565656;
    text-align: center;
    height: 20px;
    line-height: 20px;
    border-radius: 2px;
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 13px;
    padding: 0px 10px;
    color: #bbb;
    cursor: pointer;
    transition: 0.2s;
  }

  .con {
    padding: 6px;
    color: #ccc;
    font-size: 13px;
    max-height: 300px;
    margin-bottom: 6px;
    overflow: hidden;
  }

  .infoma {
    font-size: 13px;
    padding: 3px 6px;
    /* border-bottom: 1px solid #555; */
    color: #888;
    font-weight: 400;
  }

  .title:hover {
    text-decoration: underline;
  }

  .title {
    font-size: 20px;
    padding: 6px;
    cursor: pointer;
    transition: 0.2s;
  }

  .left img:hover {
    transform: rotate(360deg)
  }

  .left img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
  }

  .right {
    overflow: hidden;
    width: 630px;
    position: relative;
  }

  .left {
    float: left;
    width: 70px;
    height: 100%;
    overflow: hidden;
  }

  .content {
    overflow: hidden;
    background: #fff;
    margin-bottom: 15px;
    border-radius: 2px;
    font-size: 15px;
    font-weight: 300;
    box-shadow: 0px 1px 10px #ccc;
  }

</style>



